<template>
  <va-card
    :color="$themes.background"
    :style="computedWidgetStyle"
    :title="$t('dashboard.totalLog.title')"
  >
    <va-button
      slot="actions"
      color="info"
      class="mr-0"
      small
      flat
      @click="refresh"
    >
      <va-icon name="material-icons">autorenew</va-icon>
    </va-button>
    <va-inner-loading :loading="log.length && loading">
      <div v-if="log.length" class="tab" :style="computedStyle">
        <div class="row">
          <div class="flex xs12 xl12">
            <div
              v-for="action in log[activePage - 1]"
              :key="action._id"
              class="tab__item align--center"
            >
              <va-icon
                :name="actionIcon(action.actionName)"
                class="ml-3 mr-3"
              ></va-icon>
              <div>
                {{ $t(action.messageLocalPath) }}
                {{ action.relatedData }}
              </div>
              <div class="ml-3 mr-3">
                {{ action.user.name }}
              </div>
              <div style="text-align: center">
                {{ action.formatedDate }}
              </div>
            </div>
          </div>
        </div>
        <div class="tab__pagination_wrap">
          <va-pagination
            v-model="activePage"
            :pages="log.length"
            visiblePages="5"
            small
          ></va-pagination>
        </div>
      </div>
      <VaCardNoData
        v-else
        :loading="loading"
        LT="Log is loading..."
        ET="Empty"
      />
    </va-inner-loading>
  </va-card>
</template>

<script>
import VaCardNoData from './components/VaCardNoData'
import _ from 'lodash'
import { ActionsList } from '../../services/Users'

export default {
  name: 'dashboard-total-log',
  components: {
    VaCardNoData,
  },
  data: () => ({
    loading: false,
    activePage: 1,
    actions: [],
  }),
  computed: {
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
    computedWidgetStyle() {
      return {
        color: this.$themes.brightForeground,
      }
    },
    log() {
      return _.chunk(this.actions, 16)
    },
  },
  methods: {
    refresh() {
      this.loading = true
      ActionsList().then(({ data: actions }) => {
        this.loading = false
        this.actions = actions
      })
    },
    actionIcon(name) {
      return {
        registered: 'fa fa-user-plus',
        authenticated: 'fa fa-sign-in',
        visited: 'fa fa-map-marker',
        name_changed: 'fa fa-edit',
      }[name]
    },
  },
  mounted() {
    this.refresh()
  },
}
</script>

<style lang="scss" scoped>
.tab {
  min-height: 232px;

  &__wrap {
    max-height: 400px;
  }

  &__item {
    display: grid;
    grid-template-columns: 40px 4fr 4fr 1.5fr;
    height: 55px;
    width: calc(100% + 40px);
    padding: 0 20px;
    margin: 0 -20px;

    &-name {
      min-width: 80px;
      max-width: 80px;
    }

    &:nth-child(odd) {
      background-color: #ffffff07;
    }
  }

  &__pagination_wrap {
    display: grid;
    justify-content: center;
    height: 2rem;
  }
}
</style>
