<template>
  <va-inner-loading :loading="loading && actions.length">
    <div v-if="actions.length" class="tab" :style="computedStyle">
      <div class="tab__actions">
        <va-button
          v-for="action in actions"
          :key="action.name"
          outline
          :color="action.color"
          @click="doAPIRequest(action.url)"
          >{{ $t(action.nameLocalPath) }}</va-button
        >
      </div>
      <div class="tab__send_popup mt-3">
        <va-accordion>
          <va-collapse isOpenDefault>
            <span slot="header">
              {{ $t('dashboard.profile.actions.popup.header') }}
            </span>
            <div slot="body">
              <va-select
                :options="['Error', 'Warning', 'Info']"
                value="Error"
                v-model="customPopupType"
                class="ma-0"
                :label="$t('dashboard.profile.actions.popup.type')"
                no-clear
              />
              <va-input
                class="mb-3"
                :label="$t('dashboard.profile.actions.popup.title')"
                v-model="customPopupTitle"
              ></va-input>
              <va-input
                class="mb-3"
                :label="$t('dashboard.profile.actions.popup.message')"
                v-model="customPopupText"
              ></va-input>
              <va-button
                class="mt-2 justify--center"
                color="info"
                @click="sendPopup"
              >
                {{ $t('dashboard.profile.actions.popup.send') }}
              </va-button>
            </div>
          </va-collapse>
      </div>
    </div>
    <VaCardNoData
      v-else
      :loading="loading"
      LT="Actions are loading..."
      ET="Nothing to do"
    />
  </va-inner-loading>
</template>

<script>
import VaCardNoData from '../components/VaCardNoData'
import axios from 'axios'
import {ThrowPopup} from '../../../services/Users'

export default {
  components: {
    VaCardNoData,
  },
  props: {
    loading: Boolean,
  },
  inject: ['user'],
  data: () => ({
    customPopupType: 'Error',
    customPopupTitle: '',
    customPopupText: '',
  }),
  computed: {
    actions() {
      return this.user.actions
    },
    computedStyle() {
      return {
        opacity: this.loading ? 0.2 : 1,
      }
    },
  },
  methods: {
    sendPopup() {
      this.loading = true
      ThrowPopup(this.user.id, this.customPopupType, this.customPopupTitle, this.customPopupText).then(({data}) => {
        this.loading = false
        console.log(data)
        this.handleSuccess({
          message: 'Popup successfully sent!'
        })
      }).catch(err => {
        this.loading = false
        console.log(err.response)
        this.handleSuccess({
          message: 'Popup has no sent.'
        })
      })
    },
    handleSuccess(response) {
        this.showToast(response.message, {
          icon: 'fa-check',
          position: 'bottom-center',
          duration: 4000,
          type: 'primary',
        })
      },
      handleError(response) {
        this.showToast(response.message, {
          icon: 'fa-times',
          position: 'bottom-center',
          duration: 4000,
          type: 'primary',
        })
      },
    doAPIRequest(url) {
      console.log(window.location.protocol + '//' + window.location.host + url)
      this.loading = true
      axios
        .get(window.location.protocol + '//' + window.location.host + url)
        .then(({ data: response }) => {
          this.loading = false
          this.$root.$emit('refresh-profile')

          if (response.success) {
            if (response.action === 'sign in as') {
              sessionStorage.setItem('auth-token', response.token)
              window.location.assign(
                window.location.protocol + '//' + window.location.host
              )
            } else {
              this.showToast(`The user has been ${response.action}d!`, {
                icon: 'fa-check',
                position: 'bottom-center',
                duration: 4000,
                type: 'success',
              })
            }
          } else {
            console.log(response)
            this.showToast(`The user has not been ${response.action}d.`, {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            })
          }
        })
        .catch(({ response }) => {
          this.loading = true
          this.showToast(
            response.data.message +
              ' Status code ' +
              (response.status ?? '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
  },
}
</script>

<style lang="scss" scoped>
.tab {
  min-height: 65px;

  &__actions {
    max-width: 100%;
    display: grid;
    grid-template-columns: 1fr 1fr;
    justify-content: center;
  }
}
</style>
