<template>
  <va-card
    :color="computedWidgetColor"
    :title="$t('dashboard.support.dialogues')"
  >
    <va-button
      slot="actions"
      color="info"
      class="mr-0"
      small
      flat
      @click="updateDialogues"
    >
      <va-icon name="material-icons">autorenew</va-icon>
    </va-button>
    <div class="row align--center mb-1">
      <div class="flex xs12 sm12 md12">
        <va-input
          class="ma-0"
          :value="term"
          :placeholder="$t('tables.searchByName')"
          @input="search"
          removable
        >
          <va-icon name="fa fa-search" slot="prepend" />
        </va-input>
      </div>
    </div>

    <va-data-table
      :fields="fields"
      :data="filteredData"
      :loading="loading"
      rowClass="user-row"
    >
      <template v-slot:icon>
        <va-icon
          :name="'fa fa-user'"
          color="secondary"
        />
      </template>
      <template v-slot:starred="props">
        <va-icon
          v-if="props.rowData.starred"
          name="fa fa-star"
          color="warning"
        />
      </template>
      <template v-slot:online="props">
        <va-badge :color="getStatusColor(props.rowData.online)">
          {{ Boolean(props.rowData.online) ? 'online' : 'offline' }}
        </va-badge>
      </template>

      <template v-slot:messagesCount="props">
        <va-button small flat color="info" @click="openDialogue">
          {{ props.rowData.messagesCount }}
          <va-icon name="fa fa-envelope" class="ml-1"></va-icon>
        </va-button>
      </template>
    </va-data-table>
  </va-card>
</template>

<script>
import debounce from 'lodash/debounce'
import data from '../markup-tables/data2.json'

export default {
  data() {
    return {
      users: data.map(u => {
        u.name = u.firstName + ' ' + u.lastName
        return u
      }),
      loading: false,
      term: null,
      mode: 0,
      userRequisite: '',
    }
  },
  mounted() {
    // this.updateDialogues()
  },
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
    fields() {
      return [
        {
          name: '__slot:icon',
          width: '30px',
          dataClass: 'text-center',
        },
        {
          name: 'name',
          title: this.$t('tables.headings.name'),
          width: '30%',
        },
        {
          name: 'email',
          title: this.$t('tables.headings.email'),
          width: '50%',
        },
        {
          name: '__slot:online',
          title: this.$t('tables.headings.status'),
          width: '10%',
        },
        {
          name: '__slot:messagesCount',
          dataClass: 'text-right',
          sortField: 'online',
        },
      ]
    },
    filteredData() {
      if (!this.term || this.term.length < 1) {
        return this.users
      }

      return this.users.filter(item => {
        return (
          item.name.toLowerCase().startsWith(this.term.toLowerCase()) ||
          item.email.toLowerCase().startsWith(this.term.toLowerCase()) ||
          Object.entries(item)
            .map(prop =>
              prop[0] === 'online'
                ? prop[1]
                  ? 'online'
                  : 'offline'
                : prop[1].toString()
            )
            .includes(this.term)
        )
      })
    },
  },
  methods: {
    updateDialogues() {
      this.loading = true

      UsersList()
        .then(({ data }) => {
          this.loading = false
          this.users = data
        })
        .catch(err => {
          this.loading = false
          this.showToast(
            'There was an error when trying to fetch the users list. Status code ' +
              (err.response?.status ?? '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
    getStatusColor(status) {
      return ['danger', 'success'][+status]
    },
    search: debounce(function(term) {
      this.term = term
    }, 400),
  },
}
</script>

