<template>
  <va-card
    :color="computedWidgetColor"
    :style="computedStyle"
    :title="$t('dashboard.profile.title')"
  >
    <va-button
      @click="getMyProfile"
      class="mr-0"
      small
      outline
      slot="actions"
      color="info"
    >
      {{ $t('dashboard.profile.myProfile') }}
    </va-button>
    <div class="profile__name">
      <va-icon name="fa fa-user-circle" class="ml-2 mr-2"></va-icon>
      <div class="profile__name_first text--capitalize">
        {{ user.firstName || 'Loading...' }}
      </div>
      <div class="profile__name_last text--bold text--capitalize">
        {{ user.lastName }}
      </div>
    </div>
    <va-tabs grow v-model="tabsState" :style="computedTabsStyle">
      <va-tab v-for="(tab, i) in tabs" :key="i">
        {{
          $t(
            'dashboard.profile.tabs.' +
              tab.toLowerCase().replace('profile', '') +
              '.title'
          )
        }}
      </va-tab>
    </va-tabs>
    <component
      :loading="loadingTabs.includes(tabs[tabsState])"
      :class="['', '', 'pb-4'][this.tabsState]"
      :is="tabs[tabsState]"
    />
  </va-card>
</template>

<script>
import ProfileInfo from './dashboard-profile-tabs/ProfileInfo'
import ProfileTransactions from './dashboard-profile-tabs/ProfileTransactions'
import ProfileLog from './dashboard-profile-tabs/ProfileLog'
import ProfileActions from './dashboard-profile-tabs/ProfileActions'
import ProfileBind from './dashboard-profile-tabs/ProfileBind'
import { Me, UserById } from '../../services/Users'

export default {
  name: 'DashboardProfile',
  components: {
    ProfileInfo,
    ProfileTransactions,
    ProfileLog,
    ProfileActions,
    ProfileBind,
  },
  provide() {
    return {
      handleSuccess(response) {
        this.showToast(response.message, {
          icon: 'fa-check',
          position: 'bottom-center',
          duration: 4000,
          type: 'primary',
        })
      },
      handleError(response) {
        this.showToast(response.message, {
          icon: 'fa-times',
          position: 'bottom-center',
          duration: 4000,
          type: 'primary',
        })
      },
    }
  },
  data: () => ({
    user: {
      id: '...',
      role: '...',
      username: '...',
      transfers: '...',
      telegramUsername: '...',
      name: '',
      email: '...',
      bindedTo: '...',
      actions: [],
      transactions: [],
      log: [],
      wallets: {
        bitcoin: {
          balance: 0.1,
        },
        litecoin: {
          balance: 0.00012,
        },
        ethereum: {
          balance: 0,
        },
      },
      me: false,
    },
    loadingTabs: [
      'ProfileInfo',
      'ProfileTransactions',
      'ProfileLog',
      'ProfileActions',
    ],
    tabsState: 0,
    myRole: '',
  }),
  provide() {
    return {
      user: this.user,
    }
  },
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
      }
    },
    computedTabsStyle() {
      return {
        color: this.$themes.secondary,
      }
    },
    tabs() {
      let ts = ['ProfileInfo']

      if (this.user.role === 'user') ts.push('ProfileTransactions')
      ts.push('ProfileLog')
      ts.push('ProfileActions')
      if (this.myRole === 'manager') ts.push('ProfileBind')

      this.tabsState =
        this.tabsState > ts.length - 1 ? ts.length - 1 : this.tabsState

      return ts
    },
  },
  methods: {
    getMyProfile() {
      this.loading()
      this.$root.$emit('getting-dialogue')

      Me()
        .then(({ data: user }) => {
          this.myRole = user.role
          this.$root.$emit('open-dialogue', user)
          this.updateOpenedProfile(user, true)
        })
        .catch(err => {
          this.loaded()
          this.showToast(
            'Cannot open your profile. Status code ' +
              (err.response.status ?? '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
    updateOpenedProfile(user, me) {
      this.$root.$emit('update-wallet', user)

      this.loaded()
      this.user.me = !!me
      ;[this.user.firstName, this.user.lastName] = user.name.split(' ')

      if (Object.keys(user).length) {
        Object.keys(user).forEach(key => {
          this.user[key] = user[key]
        })
      }
    },
    refreshOpenedProfile() {
      this.$root.$emit('getting-dialogue')

      UserById(this.user.id)
        .then(({ data }) => {
          this.$root.$emit('open-dialogue', this.user)
          this.$root.$emit('open-profile', data)
        })
        .catch(err => {
          this.getMyProfile()
          this.showToast(
            'Cannot open his profile. Status code ' +
              (err.response.status ?? '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
    netToCurrency: net =>
      ({
        BTC: 'bitcoin',
        LTC: 'litecoin',
        ETH: 'ethereum',
      }[net]),
    updateBalance(currency) {
      let balance = this.user.transactions
        .filter(t => t.currency === currency)
        .reduce(
          (balance, t) =>
            balance + (t.type === 'received' ? +t.amount : -t.amount),
          0
        )

      this.user.wallets[currency.toLowerCase()].balance = balance
    },
    addTransaction(t) {
      this.user.transactions = [t, ...this.user.transactions]
      if (t.name !== 'Deposit') this.updateBalance(t.currency)
    },
    removeTransaction(transaction) {
      this.user.transactions = this.user.transactions.filter(
        t => t.id != transaction.id
      )
      if (transaction.name !== 'Deposit')
        this.updateBalance(transaction.currency)
    },
    loading() {
      this.loadingTabs = [
        'ProfileInfo',
        'ProfileTransactions',
        'ProfileLog',
        'ProfileActions',
      ]
    },
    loaded() {
      this.loadingTabs = []
    },
    gettingProfile() {
      this.loading()
    },
  },
  created() {
    this.getMyProfile()
    this.$root.$on('getting-profile', this.gettingProfile)
    this.$root.$on('open-profile', this.updateOpenedProfile)
    this.$root.$on('refresh-profile', this.refreshOpenedProfile)
    this.$root.$on('add-transaction', this.addTransaction)
    this.$root.$on('remove-transaction', this.removeTransaction)
  },
  beforeDestroy() {
    this.$root.$off('getting-profile', this.loading)
    this.$root.$off('open-profile', this.updateOpenedProfile)
    this.$root.$off('refresh-profile', this.refreshOpenedProfile)
    this.$root.$off('add-transactiff', this.addTransaction)
    this.$root.$off('remove-transaction', this.removeTransaction)
  },
}
</script>

<style lang="scss" scoped>
.profile__name {
  font-size: 1.4rem;
  margin-bottom: 0.5rem;
  justify-content: center;
  align-items: center;
  display: flex;

  &_first,
  &_last {
    margin: 0 5px;
  }
}
</style>
