<template>
  <va-inner-loading :loading="loading && log.length">
    <div v-if="log.length" class="tab" :style="computedStyle">
      <div class="row">
        <div class="flex xs12 xl12">
          <div
            v-for="action in log[activePage - 1]"
            :key="action._id"
            class="tab__item d-flex align--center"
          >
            <va-icon
              :name="actionIcon(action.actionName)"
              class="ml-3 mr-3"
            ></va-icon>
            <div>
              {{ $t(action.messageLocalPath) }}
            </div>
          </div>
        </div>
      </div>
      <div class="tab__pagination_wrap" v-if="log.length > 1">
        <va-pagination
          v-model="activePage"
          :pages="log.length"
          visiblePages="5"
          small
        ></va-pagination>
      </div>
    </div>
    <VaCardNoData v-else :loading="loading" LT="Log is loading..." ET="Empty" />
  </va-inner-loading>
</template>

<script>
import VaCardNoData from '../components/VaCardNoData'
import _ from 'lodash'

export default {
  name: 'profile-log-tab',
  components: {
    VaCardNoData,
  },
  inject: ['user'],
  data: () => ({
    activePage: 1,
  }),
  props: {
    loading: Boolean,
  },
  computed: {
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
    log() {
      return _.chunk(this.user.log, 6)
    },
  },
  methods: {
    actionIcon(name) {
      return {
        registered: 'fa fa-user-plus',
        authenticated: 'fa fa-sign-in',
        visited: 'fa fa-map-marker',
        name_changed: 'fa fa-edit',
      }[name]
    },
  },
  mounted() {
    this.activePage = 1
  },
}
</script>

<style lang="scss" scoped>
.tab {
  min-height: 232px;

  &__wrap {
    max-height: 400px;
  }

  &__item {
    height: 55px;
    width: calc(100% + 40px);
    padding: 0 20px;
    margin: 0 -20px;

    &-name {
      min-width: 80px;
      max-width: 80px;
    }

    &:nth-child(odd) {
      background-color: #ffffff07;
    }
  }

  &__pagination_wrap {
    display: grid;
    justify-content: center;
    height: 2rem;
  }
}
</style>
