<template>
  <va-card
    :color="$themes.background"
    :style="computedWidgetStyle"
    :title="$t('dashboard.support.title')"
  >
    <h6
      slot="actions"
      class="user-title"
      :style="{ color: $themes.foreground }"
    >
      <va-icon name="fa fa-user-circle" class="mr-2"></va-icon>
      {{ user.name }}
    </h6>
    <va-inner-loading :loading="!itsAdmin && loading">
      <div v-if="!itsAdmin" class="tab" :style="computedStyle">
        <chat
          :value="user.supportMessages"
          @input="sendMessage"
          height="53rem"
        />
      </div>
      <VaCardNoData
        v-else
        :loading="loading"
        LT="Fetching support messages..."
        ET="Can't talk to this person"
      />
    </va-inner-loading>
  </va-card>
</template>

<script>
import Chat from '../ui/chat/Chat'
import VaCardNoData from './components/VaCardNoData'
import * as Support from '../../services/Support'

export default {
  name: 'dashboard-support',
  components: { Chat, VaCardNoData },
  props: {
    loading: Boolean,
  },
  data: () => ({
    user: {
      id: '...',
      name: '',
      supportMessages: [],
    },
    loading: false,
    itsAdmin: false,
  }),
  methods: {
    sendMessage(message) {
      let i = this.user.supportMessages.length

      this.user.supportMessages.push(message)

      Support.sendMessage(this.user.id, message.text).then(({ data }) => {
        this.user.supportMessages[i] = data.message
        this.user.supportMessages = this.user.supportMessages.slice()
      })
    },
    gettingDialogue() {
      this.loading = true
    },
    openDialogue(user) {
      Support.fetchMessages(user.id)
        .then(({ data }) => {
          this.unread = 0
          this.loading = false
          this.itsAdmin = false
          this.user.id = user.id
          this.user.name = user.name
          this.user.supportMessages = data.messages

          this.$root.$emit('read-support', user.id)
        })
        .catch(err => {
          this.loading = false
          this.itsAdmin = true
        })
    },
  },
  computed: {
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
    computedWidgetStyle() {
      return {
        color: this.$themes.brightForeground,
      }
    },
  },
  created() {
    this.$root.$on('open-dialogue', this.openDialogue)
    this.$root.$on('getting-dialogue', this.gettingDialogue)
  },
  beforeDestroy() {
    this.$root.$off('open-dialogue', this.openDialogue)
    this.$root.$off('getting-dialogue', this.gettingDialogue)
  },
}
</script>

<style lang="scss" scoped>
.no-data {
  min-height: 65px;
  padding-top: 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}

.user-title {
  font-size: 0.8rem;
  letter-spacing: 0.06rem;
  font-weight: 500;
  text-transform: uppercase;
}
</style>
