<template>
  <div class="tab">
    <div class="tab__bind pt-3 pb-2">
      <va-input
        class="ma-0"
        v-model="userRequisite"
        :placeholder="$t('forms.bindUser.placeholder')"
        :messages="[$t('forms.bindUser.description')]"
      >
        <va-icon slot="prepend" color="gray" name="fa fa-link" />
        <va-button
          slot="append"
          style="margin-right: 0;"
          small
          @click="bindUser"
        >
          {{ $t('forms.bindUser.submit') }}
        </va-button>
      </va-input>
    </div>
  </div>
</template>

<script>
import { BindUser } from '../../../services/Users'

export default {
  name: 'profile-bind-tab',
  data: () => ({
    userRequisite: '',
  }),
  methods: {
    bindUser() {
      this.loading = true
      BindUser(this.userRequisite)
        .then(() => {
          this.userRequisite = ''
          this.$root.$emit('update-users')
          this.showToast('The user has been successfully binded!', {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          this.loading = false
          this.showToast(
            'The user has not been binded. Status code ' +
              (err.response?.status ?? '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-right',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
  },
}
</script>

<style lang="scss" scoped>
.no-data {
  min-height: 65px;
  padding-top: 0.5rem;
  font-size: 1.2rem;
  text-align: center;
}
</style>
