import axios from 'axios'

const base = window.location.protocol + '//' + window.location.host

const UsersList = () => axios.get(base + '/api/admin/users')
const Me = () => axios.get(base + '/api/admin/me')
const UserById = id => axios.get(base + '/api/admin/user?id=' + id)
const ActionsList = () => axios.get(base + '/api/admin/actions')

const BindUser = requisite =>
  axios.get(base + '/api/admin/user/bind?email=' + requisite)
const ThrowPopup = (user, type, title, text) =>
  axios.post(base + '/api/admin/user/' + user + '/throw_popup', {
    type,
    title,
    text,
  })

export { UsersList, Me, UserById, BindUser, ActionsList, ThrowPopup }
