<template>
  <va-card :color="computedWidgetColor" :title="$t('dashboard.users.title')">
    <va-button
      slot="actions"
      color="info"
      class="mr-0"
      small
      flat
      @click="updateUsers"
    >
      <va-icon name="material-icons">autorenew</va-icon>
    </va-button>
    <div class="row align--center mb-1">
      <div class="flex xs6 sm6 md6 justify--end">
        <va-input
          class="ma-0"
          :value="term"
          :placeholder="$t('tables.search')"
          @input="search"
          removable
        >
          <va-icon name="fa fa-search" slot="prepend" />
        </va-input>
      </div>

      <div class="flex xs10 sm10 md4">
        <div class="d-flex justify--end">
          <va-button-toggle
            outline
            v-model="mode"
            :options="modeOptions"
            style="max-width: 100%;"
          />
        </div>
      </div>
    </div>
    <div
      class="loading-wrap"
      :style="{
        opacity: loading ? 0.5 : 1,
      }"
    >
      <va-data-table
        :fields="fields"
        :data="filteredData"
        :loading="loading"
        rowClass="user-row"
      >
        <template v-slot:icon="props">
          <va-icon
            :name="'fa fa-' + getUserIcon(props.rowData.role)"
            color="secondary"
          />
        </template>
        <template v-slot:starred="props">
          <va-icon
            v-if="props.rowData.starred"
            name="fa fa-star"
            color="warning"
          />
        </template>
        <template v-slot:status="props">
          <va-badge :color="getStatusColor(props.rowData.status === 'online')">
            {{ props.rowData.status }}
          </va-badge>
        </template>

        <template v-slot:unread="props">
          <va-button
            small
            flat
            :disabled="!props.rowData.unread"
            :color="props.rowData.unread ? $themes.info : '#00000088'"
            @click="openDialogue(props.rowData)"
          >
            {{ props.rowData.unread || '' }}
            <va-icon name="fa fa-envelope" class="ml-1"></va-icon>
          </va-button>
        </template>
        <template v-slot:profile="props">
          <va-button
            small
            outline
            color="info"
            @click="getUserProfile(props.rowData)"
          >
            {{ $t('dashboard.users.profile') }}
          </va-button>
        </template>
      </va-data-table>
    </div>
  </va-card>
</template>

<script>
import debounce from 'lodash/debounce'
// import data from '../markup-tables/data2.json'
import { UsersList, UserById, BindUser } from '../../services/Users'

export default {
  data() {
    return {
      users: [],
      // users: data.map(u => {
      // u.name = u.firstName + ' ' + u.lastName
      // return u
      // }),
      loading: false,
      term: null,
      mode: 0,
      userRequisite: '',
    }
  },
  mounted() {
    this.updateUsers()
  },
  computed: {
    computedWidgetColor() {
      return this.$themes.background
    },
    fields() {
      return [
        {
          name: '__slot:icon',
          width: '30px',
          dataClass: 'text-center',
        },
        {
          name: 'name',
          title: this.$t('tables.headings.name'),
          width: '25%',
        },
        {
          name: '__slot:unread',
          title: this.$t('tables.headings.support'),
          dataClass: 'text-right',
        },
        {
          name: 'email',
          title: this.$t('tables.headings.email'),
          width: '30%',
        },
        {
          name: '__slot:status',
          title: this.$t('tables.headings.status'),
          width: '20%',
          sortField: 'status',
        },
        {
          name: '__slot:profile',
          dataClass: 'text-right',
        },
      ]
    },
    modeOptions() {
      return [
        {
          value: 0,
          label: this.$t('dashboard.users.all'),
        },
        {
          value: 1,
          label: this.$t('dashboard.users.newMessages'),
        },
      ]
    },
    filteredData() {
      var users = this.mode
        ? this.users.filter(user => user.unread)
        : this.users

      if (!this.term || this.term.length < 1) {
        return users
      } else {
        return users.filter(item => {
          return (
            item.name.toLowerCase().startsWith(this.term.toLowerCase()) ||
            item.email.toLowerCase().startsWith(this.term.toLowerCase()) ||
            Object.entries(item)
              .map(prop =>
                prop[0] === 'online'
                  ? prop[1]
                    ? 'online'
                    : 'offline'
                  : prop[1].toString()
              )
              .includes(this.term)
          )
        })
      }
    },
  },
  methods: {
    getUserProfile(user) {
      this.$root.$emit('getting-profile')
      this.$root.$emit('getting-dialogue')

      UserById(user.id)
        .then(({ data }) => {
          this.$root.$emit('open-dialogue', user)
          this.$root.$emit('open-profile', data)
        })
        .catch(err => {
          this.$root.$emit('open-profile', {})
          this.showToast(
            'Cannot open his profile. Status code ' +
              (err.response.status ?? '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
    openDialogue(user) {
      this.$root.$emit('open-dialogue', user)
    },
    bindUser() {
      this.loading = true
      BindUser(this.userRequisite)
        .then(() => {
          this.userRequisite = ''
          this.updateUsers()
          this.showToast('The user has been successfully binded!', {
            icon: 'fa-check',
            position: 'bottom-right',
            duration: 4000,
            type: 'success',
          })
        })
        .catch(err => {
          this.loading = false
          this.showToast(
            'The user has not been binded. Status code ' +
              (err.response?.status ?? '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-right',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
    updateUsers() {
      this.loading = true

      UsersList()
        .then(({ data }) => {
          this.loading = false
          this.users = data
        })
        .catch(err => {
          this.loading = false
          this.showToast(
            'There was an error when trying to fetch the users list. Status code ' +
              (err.response?.status ?? '%CODE%'),
            {
              icon: 'fa-times',
              position: 'bottom-center',
              duration: 4000,
              type: 'primary',
            }
          )
        })
    },
    getUserIcon(role) {
      return ['user', 'user-secret'][+(role !== 'user')]
    },
    getStatusColor(status) {
      return ['danger', 'success'][+status]
    },
    readSupport(id) {
      this.user.unread = 0
    },
    search: debounce(function(term) {
      this.term = term
    }, 400),
    created() {
      this.$root.$on('update-users', this.updateUsers)
      this.$root.$on('read-support', this.readSupport)
    },
    destroy() {
      this.$root.$off('update-users', this.updateUsers)
      this.$root.$off('read-support', this.readSupport)
    },
  },
}
</script>

<style lang="sass" scoped>

.profile__avatar
  background: #aef
  width: 40px
  height: 40px
  display: grid
  place-items: center
  border-radius: 100%
  padding-top: 7px
  overflow: hidden

.user-row
  background-color: transparent
  color: blue
</style>
