<template>
  <va-inner-loading :loading="loading">
    <div class="tab" :style="computedStyle">
      <div class="row">
        <div class="flex xs12 xl12">
          <div
            v-for="info in Object.keys(profile)"
            :key="info"
            class="tab__item d-flex align--center"
          >
            <va-icon :name="infoIcon(info)" class="ml-3 mr-3"></va-icon>
            <div class="text--bold tab__item-name mr-5">
              {{ info }}
            </div>
            <div v-if="typeof profile[info] != 'object'">
              {{ profile[info] }}
            </div>
            <div v-else>
              <span v-for="(el, i) in profile[info]" :key="i">
                <a :href="el.url" target="_blank">
                  {{ el.amount + ' ' + el.net }}
                </a>
                {{ i < profile[info].length - 1 ? ' | ' : '' }}
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </va-inner-loading>
</template>

<script>
export default {
  name: 'profile-info-tab',
  inject: ['user'],
  props: {
    loading: Boolean,
  },
  computed: {
    computedStyle() {
      return {
        color: this.$themes.brightForeground,
        opacity: this.loading ? 0.2 : 1,
      }
    },
    profile() {
      let p = {
        ID: this.user.id,
        Role: this.user.role.toUpperCase(),
        Username: this.user.username,
        'E-mail': this.user.email,
        Transfers: this.user.transfers,
      }

      if (!this.user.me) p['Binded to'] = this.user.bindedTo || 'nobody'

      return p
    },
  },
  methods: {
    infoIcon(name) {
      return {
        ID: 'fa fa-id-card-o',
        Role: 'fa fa-user-o',
        Username: 'fa fa-address-book',
        'E-mail': 'fa fa-envelope-o',
        Transfers: 'fa fa-money',
        'Binded to': 'fa fa-link',
      }[name]
    },
  },
}
</script>

<style lang="scss" scoped>
.tab {
  a {
    color: #55a1fb;
  }

  a:hover {
    text-decoration: underline;
  }

  &__item {
    min-height: 55px;
    width: calc(100% + 40px);
    padding: 15px 20px;
    margin: 0 -20px;

    &-name {
      min-width: 80px;
      max-width: 80px;
    }

    &:nth-child(odd) {
      background-color: #4d4d4d11;
    }
  }
}
</style>
